<template>
  <div class="app-container" :class="{ 'auth-page': isAuthRoute }">
    <!--  -->
    <router-view v-if="isAuthRoute" />
    
    <!--  -->
    <template v-else>
      <!--  -->
      <div class="video-background">
        <video autoplay muted loop playsinline>
          <source src="@/assets/videos/bg1.mp4" type="video/mp4">
        </video>
      </div>
      
      <el-container class="main-container">
        <!--  -->
        <el-aside width="220px" class="sidebar">
          <div class="logo-container">
            <div class="logo">
              <img src="https://yamapicgo.oss-cn-nanjing.aliyuncs.com/picgoImage/icon-rmbg.png" alt="logo" class="logo-image">
              <span></span>
            </div>
          </div>
          <el-menu
            :default-active="activeMenu"
            class="el-menu-vertical"
            router
          >
            <el-menu-item index="/">
              <el-icon><House /></el-icon>
              <span></span>
            </el-menu-item>
            <el-menu-item index="/tasks">
              <el-icon><List /></el-icon>
              <span></span>
            </el-menu-item>
            <el-menu-item index="/garden">
              <el-icon><PriceTag /></el-icon>
              <span></span>
            </el-menu-item>
            <el-menu-item index="/plant-voice">
              <el-icon><ChatDotRound /></el-icon>
              <span></span>
            </el-menu-item>
            <el-menu-item index="/plant-chat">
              <el-icon><ChatLineRound /></el-icon>
              <span></span>
            </el-menu-item>
            <el-menu-item index="/posts">
              <el-icon><Reading /></el-icon>
              <span></span>
            </el-menu-item>
            <el-menu-item index="/calendar">
              <el-icon><Calendar /></el-icon>
              <span></span>
            </el-menu-item>
          </el-menu>
          
          <div class="user-panel">
            <div class="user-info" @click="showUserMenu = !showUserMenu">
              <el-avatar :size="32" :src="userAvatar" v-if="userAvatar"></el-avatar>
              <el-avatar :size="32" icon="UserFilled" v-else></el-avatar>
              <span class="username">{{ username }}</span>
              <el-icon><CaretTop :class="{ 'rotate-icon': showUserMenu }" /></el-icon>
            </div>
            
            <div class="user-dropdown" v-if="showUserMenu">
              <div class="dropdown-item" @click="goToProfile">
                <el-icon><User /></el-icon>
                <span></span>
              </div>
              <div class="dropdown-item" @click="goToSettings">
                <el-icon><Setting /></el-icon>
                <span></span>
              </div>
              <div class="dropdown-item logout" @click="handleLogout">
                <el-icon><SwitchButton /></el-icon>
                <span></span>
              </div>
            </div>
          </div>
        </el-aside>
        
        <!--  -->
        <el-main>
          <router-view v-slot="{ Component }">
            <keep-alive>
              <component :is="Component" />
            </keep-alive>
          </router-view>
        </el-main>
      </el-container>
    </template>
  </div>
</template>

<script>
import { ref, computed, onMounted, onBeforeUnmount } from 'vue'
import { useRouter, useRoute } from 'vue-router'
import { useAuthStore } from './stores/auth'
import { usePlantStore } from './stores/plant'
import {
  Sunny, House, List, PriceTag, ChatDotRound, ChatLineRound,
  Reading, Calendar, CaretTop, User, Setting, SwitchButton
} from '@element-plus/icons-vue'
import { ElMessageBox } from 'element-plus'
import axios from 'axios'
export default {
  name: 'App',
  components: {
    Sunny, House, List, PriceTag, ChatDotRound, ChatLineRound,
    Reading, Calendar, CaretTop, User, Setting, SwitchButton
  },
  setup() {
    const router = useRouter()
    const route = useRoute()
    const authStore = useAuthStore()
    const plantStore = usePlantStore()
    
    const showUserMenu = ref(false)
    
    // //
    const isAuthRoute = computed(() => {
      const authRoutes = ['/login', '/register', '/forgot-password']
      // 
      return authRoutes.some(path => route.path.startsWith(path)) ||
             route.path.startsWith('/reset-password')
    })
    
    // 
    const activeMenu = computed(() => route.path)
    
    // 
    const username = computed(() => authStore.userInfo?.username || '')
    const userAvatar = computed(() => authStore.userInfo?.avatar || '')
    
    // 
    const handleLogout = () => {
      ElMessageBox.confirm('', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        authStore.logout()
        router.push('/login')
      }).catch(() => {})
    }
    
    // 
    const goToProfile = () => {
      showUserMenu.value = false
      router.push('/profile')
    }
    
    // 
    const goToSettings = () => {
      showUserMenu.value = false
      // router.push('/settings')
      ElMessageBox.alert('', '', {
        confirmButtonText: ''
      })
    }
    
    // 
    const handleClickOutside = (event) => {
      const userPanel = document.querySelector('.user-panel')
      if (userPanel && !userPanel.contains(event.target) && showUserMenu.value) {
        showUserMenu.value = false
      }
    }
    
    // 
    onMounted(async () => {
      document.addEventListener('click', handleClickOutside)
      
      console.log('App')
      // token
      const token = localStorage.getItem('token')
      if (token && token.trim() !== '') {
        console.log('token')
        // axios headers
        axios.defaults.headers.common['Authorization'] = `Bearer ${token}`
        
        try {
          // 
          console.log('')
          const userData = await authStore.fetchUserInfo()
          
          if (userData) {
            console.log('', userData)
            // 
            await plantStore.fetchPlants()
            console.log('')
          } else {
            console.warn('token')
            authStore.logout()
            if (!isAuthRoute.value) {
              router.push('/login')
            }
          }
        } catch (error) {
          console.error(':', error)
          authStore.logout()
          if (!isAuthRoute.value) {
            router.push('/login')
          }
        }
      } else {
        console.log('token')
        // 
        if (!isAuthRoute.value) {
          console.log('')
          router.push('/login')
        }
      }
    })
    
    onBeforeUnmount(() => {
      document.removeEventListener('click', handleClickOutside)
    })
    
    return {
      isAuthRoute,
      activeMenu,
      username,
      userAvatar,
      showUserMenu,
      handleLogout,
      goToProfile,
      goToSettings
    }
  }
}
</script>

<style>
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'PingFang SC', 'Helvetica Neue', Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #333;
  background-color: #f5f7fa;
}

/*  */
.app-container {
  min-height: 100vh;
  display: flex;
}

/*  */
.auth-page {
  background-color: #f5f7fa;
}

/*  */
.main-container {
  position: relative;
  z-index: 1;
  width: 100%;
  min-height: 100vh;
}

/*  */
.sidebar {
  background-color: #fff;
  box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.05);
  height: 100vh;
  position: fixed;
  left: 0;
  top: 0;
  z-index: 100;
  border-right: 1px solid #ebeef5;
  display: flex;
  flex-direction: column;
  width: 220px; /*  */
}

/* Logo */
.logo-container {
  height: 60px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-bottom: 1px solid #f0f0f0;
}

.logo {
  display: flex;
  align-items: center;
  font-size: 18px;
  font-weight: 600;
  color: #4caf50;
}

.logo .el-icon {
  margin-right: 10px;
  font-size: 24px;
}

/*  */
.el-menu-vertical {
  border-right: none;
  flex: 1;
}

/*  */
.el-main {
  padding: 20px;
  margin-left: 220px; /*  */
  min-height: 100vh;
  width: calc(100% - 220px); /*  */
  position: relative;
  z-index: 1;
}

/*  */
.user-panel {
  margin-top: auto;
  padding: 16px;
  border-top: 1px solid #f0f0f0;
  position: relative;
}

.user-info {
  display: flex;
  align-items: center;
  cursor: pointer;
  padding: 8px;
  border-radius: 4px;
  transition: background-color 0.3s;
}

.user-info:hover {
  background-color: #f5f7fa;
}

.username {
  margin: 0 8px;
  flex: 1;
  font-size: 14px;
  color: #333;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.rotate-icon {
  transform: rotate(180deg);
  transition: transform 0.3s;
}

/*  */
.user-dropdown {
  position: absolute;
  bottom: 100%;
  left: 0;
  right: 0;
  background-color: #fff;
  box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1);
  border-radius: 4px;
  margin: 0 16px 8px;
  overflow: hidden;
  z-index: 100;
}

.dropdown-item {
  display: flex;
  align-items: center;
  padding: 12px 16px;
  cursor: pointer;
  transition: background-color 0.3s;
}

.dropdown-item:hover {
  background-color: #f5f7fa;
}

.dropdown-item .el-icon {
  margin-right: 8px;
  font-size: 18px;
  color: #606266;
}

.dropdown-item span {
  font-size: 14px;
  color: #333;
}

.dropdown-item.logout {
  border-top: 1px solid #f0f0f0;
}

.dropdown-item.logout .el-icon,
.dropdown-item.logout span {
  color: #f56c6c;
}

/*  */
.video-background {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
  overflow: hidden;
}

.video-background video {
  width: 100%;
  height: 100%;
  object-fit: cover;
  opacity: 0.3;
}
.logo-image {
  width: 30%;
  height: 30%;
  object-fit: cover;
}
</style>