import { defineStore } from 'pinia'
import { ref, computed } from 'vue'
import axios from 'axios'
import { ElMessage } from 'element-plus'
import router from '../router'

// API
const API_URL = process.env.VUE_APP_API_URL || 'http://localhost:3001/api'

export const useAuthStore = defineStore('auth', () => {
  // 
  const user = ref(null)
  const token = ref(localStorage.getItem('token') || '')
  const loading = ref(false)
  const error = ref(null)
  
  // 
  const isAuthenticated = computed(() => !!token.value)
  const userInfo = computed(() => user.value || {})
  const isAdmin = computed(() => user.value?.role === 'admin')
  
  // 
  const login = async (credentials) => {
    loading.value = true
    error.value = null
    
    try {
      // API
      const loginData = {
        email: credentials.email,
        password: credentials.password
      }
      
      const response = await axios.post(`${API_URL}/auth/login`, loginData)
      const { token: newToken, user: userData } = response.data
      
      // token
      token.value = newToken
      user.value = userData
      
      // tokenlocalStoragecookie
      if (credentials.remember) {
        // ""localStorage
        localStorage.setItem('token', newToken)
      } else {
        // ""localStorage
        // session storagecookie
      localStorage.setItem('token', newToken)
      }
      
      // axios
      axios.defaults.headers.common['Authorization'] = `Bearer ${newToken}`
      
      ElMessage.success('!')
      return response.data
    } catch (err) {
      console.error(':', err)
      error.value = err.response?.data?.message || ''
      ElMessage.error(error.value)
      throw err
    } finally {
      loading.value = false
    }
  }
  
  // 
  const register = async (userData) => {
    loading.value = true
    error.value = null
    
    try {
      const response = await axios.post(`${API_URL}/auth/register`, userData)
      ElMessage.success('')
      return response.data
    } catch (err) {
      console.error(':', err)
      error.value = err.response?.data?.message || ''
      ElMessage.error(error.value)
      throw err
    } finally {
      loading.value = false
    }
  }
  
  // 
  const forgotPassword = async (email) => {
    loading.value = true
    error.value = null
    
    try {
      const response = await axios.post(`${API_URL}/auth/forgot-password`, { email })
      return response.data
    } catch (err) {
      console.error(':', err)
      error.value = err.response?.data?.message || ''
      ElMessage.error(error.value)
      throw err
    } finally {
      loading.value = false
    }
  }
  
  // 
  const resetPassword = async (resetData) => {
    loading.value = true
    error.value = null
    
    try {
      const response = await axios.post(`${API_URL}/auth/reset-password`, resetData)
      return response.data
    } catch (err) {
      console.error(':', err)
      error.value = err.response?.data?.message || ''
      ElMessage.error(error.value)
      throw err
    } finally {
      loading.value = false
    }
  }
  
  // 
  const fetchUserInfo = async () => {
    if (!token.value) return null
    
    loading.value = true
    error.value = null
    
    try {
      const response = await axios.get(`${API_URL}/auth/me`)
      user.value = response.data
      console.log(':', user.value)
      return response.data
    } catch (err) {
      console.error(':', err)
      if (err.response?.status === 401) {
        // 
        logout()
      }
      error.value = err.response?.data?.message || ''
      return null
    } finally {
      loading.value = false
    }
  }
  
  // 
  const logout = () => {
    // token
    user.value = null
    token.value = ''
    
    // localStoragetoken
    localStorage.removeItem('token')
    
    // axios
    delete axios.defaults.headers.common['Authorization']
    
    // 
  }
  
  // 
  const initAuth = async () => {
    if (token.value) {
      // axios
      axios.defaults.headers.common['Authorization'] = `Bearer ${token.value}`
      
      // 
      try {
        const userData = await fetchUserInfo()
        if (!userData) {
          // token
          console.warn('token')
          logout()
          return false
        }
        return true
      } catch (err) {
        console.error(':', err)
        logout()
        return false
      }
    }
    return false
  }
  
  // 
  const changePassword = async (passwordData) => {
    try {
      loading.value = true
      error.value = null
      
      const data = await axios.post(`${API_URL}/auth/change-password`, passwordData)
      ElMessage.success('')
      
      return data
    } catch (err) {
      error.value = err.response?.data?.message || ''
      ElMessage.error(error.value)
      throw err
    } finally {
      loading.value = false
    }
  }

  // 
  const updateProfile = async (profileData) => {
    try {
      loading.value = true
      error.value = null
      
      const response = await axios.put(`${API_URL}/auth/profile`, profileData)
      user.value = { ...user.value, ...response.data }
      return response.data
    } catch (err) {
      error.value = err.response?.data?.message || ''
      ElMessage.error(error.value)
      throw err
    } finally {
      loading.value = false
    }
  }
  
  return {
    // 
    user,
    token,
    loading,
    error,
    
    // 
    isAuthenticated,
    userInfo,
    isAdmin,
    
    // 
    login,
    register,
    forgotPassword,
    resetPassword,
    fetchUserInfo,
    logout,
    initAuth,
    changePassword,
    updateProfile
  }
}) 